import React from 'react';
import Loader from "../../loader/loader";
import {Redirect} from "react-router-dom";
import ProfileStatus from "./profileStatus";
import userImage from "../../../images/user.png";
import withLoader from "../../../hoc/withLoader";

const ProfileInfo = ({ setUserPhotoThunk, userInfo, isAuth, id, status, setUserStatusThunk, owner }) => {
    if (!isAuth) {
        return <Redirect to="/login" />
    }

    if (!userInfo) {
        return <Loader />;
    }

    const onInputChanged = ({ target }) => {
        if (target.files.length) {
            console.log(target.files[0])
            setUserPhotoThunk(target.files[0]);
        }
    }

    const { fullName, photos: { large }, aboutMe } = userInfo;
    return (
        <>
            <div className="main-image">
                <img src={ large ||  userImage } alt="main" />
                { owner && <input type="file" onChange={ onInputChanged }/> }
            </div>
            <h1>{ fullName }</h1>
            <div>{ aboutMe }</div>
            <ProfileStatus status={status} id={id} setUserStatusThunk={setUserStatusThunk}/>
        </>
    );


}

export default ProfileInfo;